.addCard {
   height: 240px;
   width: 240px;
   border-radius: 8px;
   border: 2px dashed #ff8933;
   margin: 20px;
   text-align: center;
   vertical-align: middle;
   background-color: rgba(255, 232, 219, 0.25);
   position: relative;

   &::after {
      content: "";
      width: 100px;
      height: 0px;
      border-top: 2px solid #ff8933;
      border-right: 2px solid #ff8933;
      margin: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
   }

   &::before {
      content: "";
      width: 0px;
      height: 100px;
      border-top: 2px solid #ff8933;
      border-right: 2px solid #ff8933;
      margin: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
   }
}

.card-options {
   width: 240px;
   height: 312px;
   border-radius: 8px;
   box-shadow: -2px 4px 8px rgba(0, 0, 0, 0.2);
   margin: 20px;

   &-wrp {
      height: 240px;
      width: 240px;
      border-radius: 8px;
      background-color: #ffe8db;
   }
}
